.store {
  display: flex;
  margin-top: 50px;

  @include breakpoint(max-width 1200px) {
    flex-wrap: wrap;
    margin-top: 30px;
  }

  &_reverse {
    @include breakpoint(max-width 1200px) {
      flex-direction: column-reverse;
    }

    .store__list-wrap {
      @include breakpoint(max-width 1200px) {
        margin-top: 50px;
      }
    }
  }

  &__list {
    margin: 0;
    margin-top: 30px;
    padding: 0;
    counter-reset: list;
    list-style: none;

    &-wrap {
      width: 447px;
      margin-right: 15px;

      @include breakpoint(max-width 1200px) {
        width: 100%;
      }
    }

    &-head {
      margin: 0;
      font-family: $GilroyExtrabold;
      font-weight: normal;
      color: $red;
      letter-spacing: 0.5px;

      img {
        margin-right: 15px;
      }
    }

    &-elem {
      position: relative;
      margin-top: 35px;
      padding-left: 40px;
      font-size: 15px;

      @include breakpoint(max-width 576px) {
        margin-top: 15px;
      }
    }

    &-elem:first-child {
      margin-top: 0;
    }

    &-elem:before {
      content: '0' counter(list);
      counter-increment: list;
      position: absolute;
      top: -4px;
      left: 0;
      font-family: $GilroyExtrabold;
      font-size: 24px;
      color: $red;
    }
  }

  &__desc-text {
    margin: 0;
    font-family: $GilroyMedium;
  }

  &__desc-img {
    margin-right: 10px;
  }

  &__gallery {
    display: flex;
    flex-direction: column;
    width: calc(100% - 462px);

    @include breakpoint(max-width 1200px) {
      width: 100%;
      margin-top: 40px;
    }

    @include breakpoint(max-width 576px) {
      margin-top: 20px;
    }

    &-row {
      display: flex;
      margin-top: 5px;

      &:first-child {
        margin-top: 0;
      }

      @include breakpoint(max-width 576px) {
        flex-wrap: wrap;
      }
    }

    &-item {
      position: relative;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-right: 10px;

      &:hover {
        opacity: 0.8;
      }

      &_portfolio:hover {
        opacity: 1;
      }

      &:nth-child(3n) {
        margin-right: 0;
      }

      @include breakpoint(max-width 1600px) {
        margin-right: 10px;

        &:nth-child(2n) {
          margin-right: 0;
        }
      }

      @include breakpoint(max-width 576px) {
        margin-top: 15px;
        margin-right: 0;
      }
    }

    &-item_bigger {
      @include breakpoint(max-width 1600px) {
        width: 62.1%;
      }

      @include breakpoint(max-width 576px) {
        width: 100%;
      }
    }

    &-item_smaller {
      @include breakpoint(max-width 1600px) {
        width: 37.9%;

        a {
          width: 100%;
          height: 100%;
        }
      }

      @include breakpoint(max-width 576px) {
        width: 100%;
      }
    }

    &-item_hide {
      @include breakpoint(max-width 1600px) {
        display: none;
      }
    }

    &-img {
      @include breakpoint(max-width 1600px) {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &-item_brief {
      flex-shrink: 0;
      width: 438px;
      background-color: $red;

      @include breakpoint(max-width 1600px) {
        flex-shrink: 1;
        width: 62.1%;
      }

      @include breakpoint(max-width 576px) {
        width: 100%;
        height: 265px;
      }

      h2 {
        width: 100%;
        margin-right: auto;
        margin-left: 15%;

        a {
          font-family: 'BebasNeueBold', sans-serif;
          font-size: 60px;
          color: white;
        }
      }
    }
  }

  &__adv {
    margin-top: 50px;

    .feedbacks__title {
      text-transform: none;
    }

    &:first-child {
      margin-top: 0;
    }

    &-desc {
      display: flex;
      align-items: flex-start;
      margin-top: 15px;
    }

    &-list {
      display: flex;
      flex-wrap: wrap;
      margin: 0;
      padding-left: 20px;
      list-style: none;
    }

    &-elem {
      position: relative;
      width: 50%;
      margin-top: 5px;
      font-size: 15px;

      @include breakpoint(max-width 500px) {
        width: 100%;
      }
    }

    &-elem:before {
      content: '';
      position: absolute;
      top: 6px;
      left: -20px;
      display: block;
      width: 10px;
      height: 10px;
      background-image: url(../img/lemon.png);
      background-repeat: no-repeat;
      background-size: cover;
    }
  }

  &-prices {
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;

    @include breakpoint(max-width 576px) {
      margin-top: 25px;
    }

    .store__adv {
      width: 48%;
      margin-right: 4%;

      &:nth-child(2) {
        margin-top: 0;
        margin-right: 0;

        @include breakpoint(max-width 576px) {
          margin-top: 30px;
        }
      }

      @include breakpoint(max-width 576px) {
        width: 100%;
        margin-right: 0;
      }
    }

  }
}

