.service__form {
  width: 100%;
  padding: 55px 61px 45px;
  box-sizing: border-box;
  box-shadow: 0 10px 80px rgba(0, 0, 0, .13);
  border-radius: 10px;
  background-color: #fff;
  @include breakpoint(max-width 768px) {
    padding: 20px;
  }
  label {
    padding-left: 22px;
    padding-bottom: 5px;
    font-size: 14px;
    color: #282828;
    font-family: GilroySemibold, sans-serif;
    font-weight: 400;
  }
  &-head {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    &_item {
      position: relative;
      display: flex;
      flex-direction: column;
      padding-bottom: 20px;

      @include breakpoint(max-width 1200px) {
        margin-bottom: 20px;
      }
    }
  }
  &_input {
    width: 180px;
    border: 1px solid #e9e9e9;
    border-radius: 25px;
    background-color: #fff;
    padding: 13px 22px;
  }
  &-submit {
    margin: 30px 0;
    font-family: GilroySemibold, sans-serif;
    font-size: 16px;
    text-align: center;
    background-color: #ff5038;
    border-radius: 25px;
    padding: 13px 0;
    border: 0;
    color: #fff;
    width: 220px;
    cursor: pointer;
    transition: all .2s;
    &:hover {
      transform: scale(.9);
      transition: all .2s;
    }
  }
  &-message {
    display: flex;
    margin-top: 10px;
    @include breakpoint(max-width 992px) {
      flex-direction: column;
      align-items: center;
    }
  }
  &-textarea {
    display: flex;
    flex-direction: column;
    width: 75%;
    @include breakpoint(max-width 992px) {
      width: 100%;
    }
  }
  &-file {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 25%;
    padding-top: 23px;

    @include breakpoint(max-width 992px) {
      width: 100%;
    }
  }
  .input-file {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.01;
    cursor: pointer;
    top: 0;
    font-size: 6px;
    left: 0;
  }
  .btn-file__wrap {
    position: relative;
    width: 180px;
    text-align: center;
    transition: all .2s;
    cursor: pointer;

    @include breakpoint(max-width 992px) {
      margin-top: 10px;
    }
  }
  .btn-input-file {
    display: flex;
    align-items: center;
    transition: transform 0.2s;
    img {
      padding-right: 5px;
    }
    span {
      font-weight: 700;
      margin-top: 5px;
    }
  }
  &-files {
    color: #aaaaaa;
    font-size: 11px;
    margin-top: 5px;
  }
  textarea {
    width: 100%;
    min-height: 104px;
    border-radius: 10px;
    resize: none;
    border: 1px solid #e9e9e9;
    background-color: #fff;
    padding: 10px 22px;
    box-sizing: border-box;
  }
  &-desc {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
    &_span {
      font-size: 14px;
      font-weight: 700;
      color: #282828;
      text-align: center;
      @include breakpoint(max-width 576px) {
        margin-bottom: 10px;
      }
    }
    &_red {
      color: #ff4834;
    }
    @include breakpoint(max-width 576px) {
      flex-direction: column;
      justify-content: center;
    }
  }
}

.service__form .btn-file__wrap:hover .btn-input-file {
  transform: scale(.9);
  transition: transform 0.2s;
}

